import { Component, OnInit } from '@angular/core';
import { NeutrinosOAuthClientService } from './neutrinos-oauth-client.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'lib-neutrinos-oauth',
  template: `
   NOTE: use this component only for testing. Not intended for production

      <button (click)="login()" >  Login </button>
      <button (click)="token()" > Get tokenset</button>
      <button (click)="logout()" > Logout </button>
      <button (click)="userinfo()" > In memory user info </button>
      <button (click)="httpTest()" > Http Interceptor test </button>
      `,
  styles: []
})
export class NeutrinosOauthComponent implements OnInit {

  constructor(public neutrinosOauthService: NeutrinosOAuthClientService, private http: HttpClient) { }

  ngOnInit() {
    this.neutrinosOauthService.authState().subscribe(state => {
      console.log(state);
    });
  }

  login() {
    const a = this.neutrinosOauthService.login('/i-am-loggedin').then(tokens => {
      console.log('got tokens in component', tokens);
    }).catch(e => {
      console.error(e);
    });
    // console.log(a);
  }

  token() {
    this.neutrinosOauthService.getUserInfo().then(t => {
      console.log('t', t);
    }).catch(console.log);
  }

  logout() {
    this.neutrinosOauthService.logout()
  }

  userinfo() {
    alert(JSON.stringify(this.neutrinosOauthService.userInfo))
    console.log(this.neutrinosOauthService.userInfo);
  }

  httpTest() {
    this.http.get('http://localhost:8081/api/ping').subscribe(res => {
      console.log(res);
    }, e => {
      console.error(e);
    });
  }

}
