import { Injectable } from '@angular/core';
import { ActivatedRoute, RouterStateSnapshot, Router, Route, ActivatedRouteSnapshot } from '@angular/router';
import { NeutrinosOAuthClientService } from './neutrinos-oauth-client.service';

@Injectable({
  providedIn: 'root'
})
export class NeutrinosAuthGuardService {

  private currUrl;
  
  constructor(private _neutrinosOAuthClientService: NeutrinosOAuthClientService, private _router: Router, private _arouter: ActivatedRoute) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<boolean>   {
    
    return new Promise ((resolve, reject) => {
      this.currUrl = state.url;
      if(this._neutrinosOAuthClientService.isLoggedIn) {
        return resolve(true);
      } else {
        this._neutrinosOAuthClientService.login().then(tokenSet => {
          this._router.navigate([this.currUrl]);
          return resolve(true);
        })
        .catch(err => {
          console.error(err);
          if(err && err.code == 'auth-cancel') {
            // Added setTimeout for reloading the current route
            setTimeout(() => {
              this._router.navigateByUrl(this.currUrl,{skipLocationChange: true}).then(() => {
                return resolve(false);
              });
            }, 500);
          } else {
            return resolve(false);
          }
        })
      }
    })
  } 

}
